@import "~office-ui-fabric-react/dist/css/fabric.min.css";

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.app {
  box-sizing: border-box;
  max-width: 390px;

  margin: auto;

  .Main {
    padding: 20px;
    background: gainsboro;
    position: relative;
    min-height: 680px;

    .rmc-notification {
      position: absolute;
      top: unset !important;
      bottom: 20px;
    }

  }

  .AccountList {
    .Account {
      margin-top: 12px;
      box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 5px 0px;
      background-color: rgb(255, 255, 255);
      padding: 14px 14px 0;

      .Account-panel {
        display: flex;
        flex-direction: column;

        .code-row {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;

          .tfa-code {
            //font-size: 36px;
            cursor: pointer;
            user-select: none;

            transition: transform 200ms linear;
            transform: scale(1);

            &:active {
              transform: scale(1.15);
            }

            //color: red;
          }

        }
      }

      &:first-child {
        //margin-top: 0;
      }

      &.noTimeToDo {
        .tfa-code {
          color: rgb(166, 166, 166);
        }
      }
    }
  }

  .SearchBox {
    margin-top: 12px;
  }

  .AddForm {
    margin-top: 12px;


    .addButton {
      margin-top: 12px;

      .ms-Button {
        width: 100%;
      }
    }

  }

  .SettingForm {
    .sync-btn {
      padding-top: 12px;
      display: flex;

      .ms-Button {
        flex: 1;
      }

      .ms-Spinner {
        margin-left: 6px;
        margin-right: 6px;
        flex: 0 0 auto;
      }
    }
    .user-sync-btn {
      padding-top: 12px;
      display: flex;
      flex-direction: column;
      .ms-Button {
        flex: 1;
      }

      .time-set-div {
        margin-top: 20px;
        box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1), 0 0 4px 0 rgba(0,0,0,.08);
        display: flex;
        flex-direction: column;
        align-items: stretch;
        background-color: white;
        .picky-date-time233{
          display: none;
          box-shadow: none;
          padding: 0;
          width: 100%;
          text-align: center;

          .picky-date-time__close{
            display: none;
          }

          .picky-date-time-calendar__button{
            position: unset;
            width: 90%;
          }

          .picky-date-time__calendar{
            margin-bottom: 0;
          }

          .picky-date-time__clock{
            margin-bottom: 0;
            padding-bottom: 12px;
          }

          &.visible{
            display: block;
          }

        }
        //margin-left: 6px;
        //margin-right: 6px;
        //flex: 0 0 auto;

        .ms-Button{
          //margin-top: 4px;
          flex: unset;
        }

      }
    }
  }


}

